
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}

.from-group {
  position: relative;
}
.from-group input {
  outline: none;
  border: solid 2px #029898;
  background: none;
  -webkit-box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
          box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}
.from-group input:valid ~ label, .from-group input:focus ~ label {
  top: -35%;
  left: 0;
}
.from-group label {
  color: #029898;
  font-size: 0.5em;
  position: absolute;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  top: 50%;
  left: 1em;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 2px;
  pointer-events: none;
}

